    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    color: #c78f22;
  }
  
  .error-indicator img {
    margin-bottom: 1rem;
  }
  
  .error-indicator .boom {
